<template>
  <div class="root">
    <div class="section" v-for="(section, index) in this.requestDetail" :key="index">
      <div class="section-head" @click="foldSectionView(Object.keys(section)[0])">
        <div class="section-title">{{Object.keys(section)[0]}}</div>
        <div
          class="section-operate"
        >{{unfoldSection[Object.keys(section)[0]] ? " [click to fold] " : " [click to unfold] "}}</div>
      </div>
      <div v-if="unfoldSection[Object.keys(section)[0]]">
        <div v-if="isStringContent(Object.values(section)[0])">
          <div class="response-content">
            <vue-json-pretty v-if="isValidJson(Object.values(section)[0])" :path="'res'" :showLine=true :data="JSON.parse(Object.values(section)[0])"></vue-json-pretty>
            <pre v-else id="response-content">{{jsonFormateString(Object.values(section)[0])}}</pre>
          </div>
        </div>
        <div v-else>
          <div v-for="(item, key) in Object.values(section)[0]" :key="key">
            <div v-if="hasSubItems(item[Object.keys(item)[0]])">
              <div
                class="left left-padding"
                @click="foldView(Object.keys(item)[0])"
              >{{Object.keys(item)[0]}}</div>
              <div>
                <div
                  :class="[subkey % 2 ? 'row-color' : '', 'row']"
                  v-for="(subitem, subkey) in item[Object.keys(item)[0]]"
                  :key="subkey"
                  :style="rowStyle(subkey)"
                >
                  <div class="left sub-row">{{Object.keys(subitem)[0]}}</div>
                  <div class="right">{{subitem[Object.keys(subitem)[0]]}}</div>
                </div>
              </div>
            </div>
            <div v-else>
              <div :class="[key % 2 ? 'row-color' : '', 'row']">
                <div class="left left-padding">{{Object.keys(item)[0]}}</div>
                <div class="right">{{item[Object.keys(item)[0]]}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueJsonPretty from "vue-json-pretty";

export default {
  name: "HelloWorld",
  props: ["requestDetail"],
  components: {
    VueJsonPretty,
  },
  computed: {
    overviewKeys() {
      return Object.keys(this.requestDetail.Overview);
    },
  },
  data() {
    return {
      unfoldSection: {
        "Request Query": true,
        "Response Data": true,
      },
    };
  },
  methods: {
    rowStyle: function (index) {
      let result = {};
      if (index % 2 === 1) {
        result.backgroundColor = "#f3f3f3";
      } else {
        result.backgroundColor = "#ffffff";
      }
      return result;
    },
    hasSubItems: function (item) {
      return Array.isArray(item);
    },
    isStringContent: function (item) {
      return typeof item === "string";
    },
    objectKeys: function (obj) {
      return Object.keys(obj);
    },
    foldSectionView: function (key) {
      if (this.unfoldSection[key]) {
        this.$set(this.unfoldSection, key, false);
      } else {
        this.$set(this.unfoldSection, key, true);
      }
    },
    jsonFormateString: function (input) {
      if (input.length > 0 && this.isValidJson(input)) {
        return JSON.stringify(JSON.parse(input), null, 4);
      } else if (input.startsWith("<?xml")) {
        return this.xmlFormateString(input);
      }
      return input;
    },
    xmlFormateString: function (xml) {
      // return beautify(xml);
      var reg = /(>)\s*(<)(\/*)/g; // updated Mar 30, 2015
      var wsexp = / *(.*) +\n/g;
      var contexp = /(<.+>)(.+\n)/g;
      xml = xml
        .replace(reg, "$1\n$2$3")
        .replace(wsexp, "$1\n")
        .replace(contexp, "$1\n$2");
      var formatted = "";
      var lines = xml.split("\n");
      var indent = 0;
      var lastType = "other";
      // 4 types of tags - single, closing, opening, other (text, doctype, comment) - 4*4 = 16 transitions
      var transitions = {
        "single->single": 0,
        "single->closing": -1,
        "single->opening": 0,
        "single->other": 0,
        "closing->single": 0,
        "closing->closing": -1,
        "closing->opening": 0,
        "closing->other": 0,
        "opening->single": 1,
        "opening->closing": 0,
        "opening->opening": 1,
        "opening->other": 1,
        "other->single": 0,
        "other->closing": -1,
        "other->opening": 0,
        "other->other": 0,
      };

      for (var i = 0; i < lines.length; i++) {
        var ln = lines[i];

        // Luca Viggiani 2017-07-03: handle optional <?xml ... ?> declaration
        if (ln.match(/\s*<\?xml/)) {
          formatted += ln + "\n";
          continue;
        }
        // ---

        var single = Boolean(ln.match(/<.+\/>/)); // is this line a single tag? ex. <br />
        var closing = Boolean(ln.match(/<\/.+>/)); // is this a closing tag? ex. </a>
        var opening = Boolean(ln.match(/<[^!].*>/)); // is this even a tag (that's not <!something>)
        var type = single
          ? "single"
          : closing
          ? "closing"
          : opening
          ? "opening"
          : "other";
        var fromTo = lastType + "->" + type;
        lastType = type;
        var padding = "";

        indent += transitions[fromTo];
        for (var j = 0; j < indent; j++) {
          padding += "\t";
        }
        if (fromTo == "opening->closing")
          formatted = formatted.substr(0, formatted.length - 1) + ln + "\n";
        // substr removes line break (\n) from prev loop
        else formatted += padding + ln + "\n";
      }

      return formatted;
    },
    isValidJson: function (input) {
      try {
        JSON.parse(input);
        return true;
      } catch (err) {
        return false;
      }
    },
  },
};
</script>

