<template>
  <div id="root">
    <ul class="el-menu">
      <li v-for="(item, tabIndex) in menuItems" :key="tabIndex" :class="['el-menu-item', tabIndex == selectedIndex ? 'is-active' : '']" @click="selectMenuItem(tabIndex)">{{item}}</li>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: ["activeIndex", "menuItems"],
  data() {
      return {
          selectedIndex: this.activeIndex
      }
  },
  methods: {
      selectMenuItem(tabIndex) {
          this.selectedIndex = tabIndex;
          this.$emit("menubarselected", tabIndex);
      }
  }
}
</script>

