<template>
  <div id="log">
    <div class="operate">
      <div class="log-on">capture log</div>
      <toggle-button class="switch" :labels="true" :value="recordLog" color="#1188ff" :height="20"  @change="onChangeEventHandler"/>
      <div class="button" @click="clearLog">clear log</div>
    </div>
    <pre v-for="(item, index) in logList" :key="index" class="pre" v-bind:style="'color: ' + item.color + ' !important'">>{{item.message}}</pre>
  </div>
</template>

<script>

import Vue from "vue"
import ToggleButton from 'vue-js-toggle-button'
Vue.use(ToggleButton)

export default {
  name: 'HelloWorld',
  props: {
    logList: Array,
    recordLog: Boolean
  },
  methods: {
    clearLog() {
      this.$emit("sendCommand", {control: "clearLog"});
    },
    onChangeEventHandler(event) {
      let record = event.value ? "startRecordLog" : "stopRecordLog";
      this.$emit("sendCommand", {control: record});
    }
  }
}
</script>

