<template>
  <div id="app">
    <MenuBar :activeIndex="activeIndex" :menuItems="menuItems" @menubarselected="menubarselected"></MenuBar>
    <LogView v-if="activeIndex == 0" :logList="logList" :recordLog="recordLog" @sendCommand="controlCenter" />
    <div v-if="activeIndex == 1">
      <NetworkView :networkRequest="networkRequest" :recordRequest="recordRequest" @sendCommand="controlCenter" @showNetworkDetail="showNetworkDetail"></NetworkView>
      <NetworkDetailView v-if="showDetail" :requestDetail="detailData"></NetworkDetailView>
    </div>
  </div>
</template>

<script>
import MenuBar from "./components/menu-bar.vue"
import LogView from "./components/log-view.vue";
import NetworkView from "./components/network-view.vue";
import NetworkDetailView from "./components/network-detail.vue";

var host = document.location.hostname;
var ws = new WebSocket("ws://"+host+":9000", "echo-protocol");

export default {
  name: "App",
  components: {
    LogView,
    NetworkView,
    NetworkDetailView,
    MenuBar
  },
  data() {
    return {
      menuItems: ['console log', 'network request'],
      networkRequest: {},
      logList: [],
      showDetail: false,
      detailData: {},
      activeIndex: 0,
      recordLog: true,
      recordRequest: true
    };
  },
  mounted() {
    ws.onopen = () => {
      this.receiveLog({ message: "CONNECTED", color: "#00ff00" });
    };
    ws.onclose = () => {
      this.receiveLog({ message: "DISCONNECTED", color: "#ff7200" });
    };
    ws.onmessage = (event) => {
      this.eventHandler(event);
    };
  },
  methods: {
    receiveLog: function (msg) {

      if (this.logList.length > 5000) {
        this.logList = []
      }

      this.logList.push(msg);
    },
    eventHandler: function (event) {
      const result = JSON.parse(event.data);
      if (result && typeof result === "string") {
        this.receiveLog({ message: event.data, color: "#333333" });
      } else {
        switch (result.type) {
          case "log":
            if (this.recordLog) {
              this.receiveLog(result);
            }
            break;
          case "network":
            if (this.recordRequest) {
              this.$set(this.networkRequest, result.taskID, result);
            }
            break;
          default:
            break;
        }
      }
    },
    showNetworkDetail: function (rowData) {
      this.detailData = rowData.fullData;
      this.showDetail = true;
    },
    handleSelect: function (index) {
      this.activeIndex = index;
    },
    menubarselected: function(tabIndex) {
      this.activeIndex = tabIndex;
    },
    controlCenter: function(commends) {
      switch (commends.control) {
          case "clearLog":
            this.logList = [];
            break;
          case "clearRequest":
            this.networkRequest = {};
            break;
          case "stopRecordLog":
            this.recordLog = false;
            break;
          case "startRecordLog":
            this.recordLog = true;
            break;
          case "stopRecordRequest":
            this.recordRequest = false;
            break;
          case "startRecordRequest":
            this.recordRequest = true;
            break;
          default:
            this.sendMessage(commends);
            break;
        }
    },
    sendMessage: function(msg) {
      ws.send(msg);
    }
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
}

</style>
