<template>
  <div class="root">
    <div class="operate">
      <div class="log-on">capture network request</div>
      <toggle-button class="switch" :labels="true" :value="recordRequest" color="#1188ff" :height="20"  @change="onChangeEventHandler"/>
      <div class="button" @click="clearRequest">clear all</div>
    </div>
    <v-table
      is-horizontal-resize
      column-width-drag
      style="width:100%"
      :height="400"
      :columns="columns"
      :table-data="networkList"
      row-hover-color="rgba(17, 136, 255, 0.4)"
      row-click-color="rgba(17, 136, 255, 0.5)"
      odd-bg-color="#ffffff"
      even-bg-color="#ECF3FF"
      :row-height="30"
      :column-cell-class-name="columnCellClass"
      :row-click="rowClick"
    ></v-table>
  </div>
</template>

<script>
import Vue from "vue";
// 引入样式
import "vue-easytable/libs/themes-base/index.css";
// 导入 table 和 分页组件
import { VTable, VPagination } from "vue-easytable";

// 注册到全局
Vue.component(VTable.name, VTable);
Vue.component(VPagination.name, VPagination);

import ToggleButton from 'vue-js-toggle-button'
Vue.use(ToggleButton)

export default {
  name: "HelloWorld",
  props: ["networkRequest", "recordRequest"],
  computed: {
    networkList() {
      return Object.values(this.networkRequest);
    },
  },
  methods: {
    rowClick: function (rowIndex, rowData) {
      this.$emit("showNetworkDetail", rowData);
    },
    columnCellClass() {
      return "custom-cell";
    },
    onChangeEventHandler(event) {
      let record = event.value ? "startRecordRequest" : "stopRecordRequest";
      this.$emit("sendCommand", {control: record});
    },
    clearRequest() {
      this.$emit("sendCommand", {control: "clearRequest"});
    },
  },
  data() {
    return {
      columns: [
        {
          field: "code",
          title: "Response Code",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "host",
          title: "Host",
          width: 150,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "path",
          title: "Path",
          width: 180,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "method",
          title: "Method",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "status",
          title: "Status",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "startDate",
          title: "StartDate",
          width: 80,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "duration",
          title: "Duration",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "mimeType",
          title: "MIMEType",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
        {
          field: "size",
          title: "Size",
          width: 50,
          titleAlign: "center",
          columnAlign: "center",
          isResize: true,
        },
      ],
    };
  },
};
</script>

